<template>
  <div class="grid-content">
    <div class="quContent">
      <div class="quTitle">{{ quNum }}</div>
      <div class="quBody">
        <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'QuSelect',
  props: {
    quNum: {type: String, default: ''}
  },
  data () {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: ''
    }
  },
  methods: {
    clickText (id) {
      this.$emit('show-text-editor', id)
    }
  }
}
</script>

<style scoped>
.quContent{
  text-align: left;
  padding: 0px 10px;
}
.quTitle{
  font-size: 14px;
  padding: 10px 0px;
}
.quTitle{
  padding-top: 0px;
}
</style>



