<template>
  <el-form ref="form" label-width="80px" size="mini">
    <el-form-item v-show="survey.questions[index].quType!=='FILLBLANK'" label="">
      <el-radio-group v-model="inputProp.commonAttr.isRequired">
        <el-radio :label="1"></el-radio>
        <el-radio :label="0"></el-radio>
      </el-radio-group>
    </el-form-item>
    <!--    <div>{{ inputProp }}</div>-->
    <el-form-item label="">
      <el-select v-model="inputProp.commonAttr.checkType" placeholder="" @change="changeCheckType">
        <el-option
          v-for="item in checkType"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select v-show="inputProp.commonAttr.checkType === 'TIME'" v-model="inputProp.dateTimeAttr.dateFormat" placeholder="" style="width: 290px;" >
        <el-option :value="5" label="HH:mm:ss"></el-option>
        <el-option :value="6" label="HH:mm"></el-option>
        <el-option :value="7" label="HH:mm"></el-option>
      </el-select>
      <el-select v-show="inputProp.commonAttr.checkType === 'DATE'" v-model="inputProp.dateTimeAttr.dateFormat" placeholder="" style="width: 290px;">
        <el-option :value="3" label="YYYY-MM-DD"></el-option>
        <el-option :value="2" label="YYYY-MM"></el-option>
        <el-option :value="1" label="YYYY"></el-option>
      </el-select>
      <el-select v-show="inputProp.commonAttr.checkType === 'DATETIME'" v-model="inputProp.dateTimeAttr.dateFormat" placeholder="" style="width: 290px;">
        <el-option :value="4" label="YYYY-MM-DD HH:mm:ss"></el-option>
        <!--        <el-option :value="8" label="YYYY-MM-DD HH:mm"></el-option>-->
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="inputProp.commonAttr.placeholder"/>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="inputProp.commonAttr.defaultValue"/>
    </el-form-item>
    <!--    v-show="inputProp.commonAttr.checkType==='' || inputProp.commonAttr.checkType === 'NO' || inputProp.commonAttr.checkType === 'UNSTRCN' || inputProp.commonAttr.checkType === 'STRCN'"-->
    <el-form-item label="" >
      <el-input-number v-model="inputProp.commonAttr.inputRow" :min="1" />&nbsp;
    </el-form-item>

    <el-form-item v-show="inputProp.commonAttr.checkType === 'TIME' || inputProp.commonAttr.checkType === 'DATE' || inputProp.commonAttr.checkType === 'DATETIME'" label="">
      <el-checkbox-group v-model="inputProp.dateTimeAttr.attrs" @change="changeDateAttrs">
        <!--        <el-checkbox label="show-now" ></el-checkbox>-->
        <el-checkbox v-show="inputProp.commonAttr.checkType === 'DATE'" label="more" ></el-checkbox>
        <el-checkbox v-show="inputProp.dateTimeAttr.dateFormat!==1" label="range" ></el-checkbox>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item v-show="(inputProp.commonAttr.checkType === 'TIME' && inputProp.dateTimeAttr.dateFormat===7) || (inputProp.commonAttr.checkType === 'TIME' && inputProp.dateTimeAttr.dateFormat!==7 && !inputProp.dateTimeAttr.attrs.includes('range'))" label="">
      <el-time-picker v-show="inputProp.dateTimeAttr.dateFormat===7 || (inputProp.dateTimeAttr.dateFormat!==7 && !inputProp.dateTimeAttr.attrs.includes('range'))" v-model="inputProp.dateTimeAttr.timeRange.range" format="HH:mm" value-format="HH:mm" is-range style="margin-right: 10px;width: 300px;" ></el-time-picker>
      <span v-show="inputProp.dateTimeAttr.dateFormat === 7"> <el-time-picker v-model="inputProp.dateTimeAttr.timeRange.step" format="HH:mm" value-format="HH:mm" style="width: 100px;" ></el-time-picker></span>
    </el-form-item>

    <el-form-item v-show="inputProp.commonAttr.checkType === 'NUM' || inputProp.commonAttr.checkType === 'DIGITS'" label="">
      <span>
         <el-input-number v-model="inputProp.numAttr.min" style="margin-right: 10px;"></el-input-number>
         <el-input-number v-model="inputProp.numAttr.max" ></el-input-number>
      </span>
    </el-form-item>
    <el-form-item v-show="inputProp.commonAttr.checkType === 'NUM'" label="" style="display: none;">
      <el-input-number v-model="inputProp.numAttr.precision"/>&nbsp;
    </el-form-item>

    <el-form-item v-show="inputProp.commonAttr.checkType !== 'NUM' && inputProp.commonAttr.checkType !== 'DIGITS' && inputProp.commonAttr.checkType !== 'TIME' && inputProp.commonAttr.checkType !== 'DATE' && inputProp.commonAttr.checkType !== 'DATETIME'" label="">
       <el-input-number v-model="inputProp.commonAttr.minlength" style="margin-right: 10px;"></el-input-number>
       <el-input-number v-model="inputProp.commonAttr.maxlength" ></el-input-number>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'DwInputProps',
  model: {
    prop: 'inputProp',
    event: 'update-input-prop'
  },
  props: {
    index: {type: Number, default: 0},
    inputProp: {type: Object, default: () => {}},
    survey: {type: Object, default: () => {}}
  },
  data () {
    return {
      checkType: [
        {value: 'NO', label: ''},
        {value: 'EMAIL', label: 'Email'},
        {value: 'NUM', label: ''},
        {value: 'DIGITS', label: ''},
        {value: 'TEL_PHONE', label: ''},
        {value: 'TIME', label: ''},
        {value: 'DATE', label: ''},
        {value: 'DATETIME', label: ''},
        {value: 'ZIPCODE', label: ''},
        {value: 'TEL', label: ''},
        {value: 'PHONE', label: ''},
        {value: 'IDENT_CODE', label: ''},
        {value: 'URL', label: ''},
        {value: 'UNSTRCN', label: ''},
        {value: 'STRCN', label: ''}
      ]
    }
  },
  methods: {
    changeCheckType () {
      const checkType = this.inputProp.commonAttr.checkType
      const dateFormat = this.inputProp.dateTimeAttr.dateFormat
      if (checkType === 'TIME' && dateFormat !== 5 && dateFormat !== 6) {
        this.inputProp.dateTimeAttr.dateFormat = 5
      } else if (checkType==='DATE' && dateFormat !== 1 && dateFormat !== 2 && dateFormat !== 3) {
        this.inputProp.dateTimeAttr.dateFormat = 3
      } else if (checkType==='DATETIME') {
        this.inputProp.dateTimeAttr.dateFormat = 4
      }
    },
    changeDateAttrs (val) {
      console.debug('val', val)
      // 
      // this.inputProp.dateTimeAttr.attrs = val && val.length>0 ? [val[val.length-1]] : []
      // show-now more range
      if (val) {
        const temp = val.map((item, index) => {
          if (item === 'more' || item==='range') return item
        })
        const newVal = val.map((item, index) => {
          if (!(item === 'more' || item==='range')) return item
        })
        if (temp.length>0) newVal.push(temp[temp.length-1])
        this.inputProp.dateTimeAttr.attrs = newVal
      }
      console.debug('inputProp.dateTimeAttr.attrs', this.inputProp.dateTimeAttr.attrs)
    }
  }
}
</script>

<style scoped>

</style>



